"use client"

import { useState } from 'react'
import Layout from '@/components/shared/Layout'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface TrafficSignal {
  id: string
  location: string
  status: 'red' | 'yellow' | 'green'
  mode: 'auto' | 'manual'
  condition: 'operational' | 'maintenance' | 'fault'
  trafficDensity: number
  lastMaintenance: string
}

export default function TrafficSignalsPage() {
  const [selectedSignal, setSelectedSignal] = useState<string>('all')

  const signals: TrafficSignal[] = [
    {
      id: "SIG-001",
      location: "Main Gate",
      status: "green",
      mode: "auto",
      condition: "operational",
      trafficDensity: 65,
      lastMaintenance: "2024-01-15"
    },
    {
      id: "SIG-002",
      location: "Loading Bay A",
      status: "red",
      mode: "manual",
      condition: "operational",
      trafficDensity: 85,
      lastMaintenance: "2024-01-10"
    },
    {
      id: "SIG-003",
      location: "Intersection B",
      status: "yellow",
      mode: "auto",
      condition: "maintenance",
      trafficDensity: 30,
      lastMaintenance: "2024-01-20"
    }
  ]

  const getStatusColor = (status: TrafficSignal['status']) => {
    const colors = {
      red: 'bg-red-500',
      yellow: 'bg-yellow-500',
      green: 'bg-green-500'
    }
    return colors[status]
  }

  const getConditionBadge = (condition: TrafficSignal['condition']) => {
    const styles = {
      operational: 'bg-green-100 text-green-800',
      maintenance: 'bg-yellow-100 text-yellow-800',
      fault: 'bg-red-100 text-red-800'
    }
    return styles[condition]
  }

  return (
    <Layout title="Traffic Signals Management">
      <div className="space-y-6">
        {/* Overview Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Total Signals</h3>
            <p className="text-2xl font-bold">{signals.length}</p>
          </Card>
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Operational</h3>
            <p className="text-2xl font-bold text-green-600">
              {signals.filter(s => s.condition === 'operational').length}
            </p>
          </Card>
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">In Maintenance</h3>
            <p className="text-2xl font-bold text-yellow-600">
              {signals.filter(s => s.condition === 'maintenance').length}
            </p>
          </Card>
          <Card className="p-4">
            <h3 className="text-sm font-medium text-gray-500">Average Traffic</h3>
            <p className="text-2xl font-bold text-blue-600">
              {Math.round(signals.reduce((acc, sig) => acc + sig.trafficDensity, 0) / signals.length)}%
            </p>
          </Card>
        </div>

        {/* Signal Controls */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {signals.map((signal) => (
            <Card key={signal.id} className="p-6">
              <div className="space-y-4">
                <div className="flex justify-between items-start">
                  <div>
                    <h3 className="text-lg font-semibold">{signal.location}</h3>
                    <p className="text-sm text-gray-500">ID: {signal.id}</p>
                  </div>
                  <Badge className={getConditionBadge(signal.condition)}>
                    {signal.condition.charAt(0).toUpperCase() + signal.condition.slice(1)}
                  </Badge>
                </div>

                {/* Signal Status Indicator */}
                <div className="flex space-x-2">
                  {['red', 'yellow', 'green'].map((color) => (
                    <div
                      key={color}
                      className={`w-8 h-8 rounded-full ${
                        signal.status === color ? getStatusColor(signal.status as TrafficSignal['status']) : 'bg-gray-200'
                      }`}
                    />
                  ))}
                </div>

                {/* Traffic Density */}
                <div>
                  <div className="flex justify-between mb-1">
                    <span className="text-sm text-gray-600">Traffic Density</span>
                    <span className="text-sm font-medium">{signal.trafficDensity}%</span>
                  </div>
                  <Progress value={signal.trafficDensity} />
                </div>

                {/* Controls */}
                <div className="space-y-2">
                  <div className="flex justify-between items-center">
                    <span className="text-sm">Mode:</span>
                    <Select defaultValue={signal.mode}>
                      <SelectTrigger className="w-[120px]">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="auto">Auto</SelectItem>
                        <SelectItem value="manual">Manual</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>

                  <div className="grid grid-cols-3 gap-2">
                    <Button 
                      variant={signal.status === 'red' ? 'default' : 'outline'}
                      className="w-full"
                    >
                      Red
                    </Button>
                    <Button 
                      variant={signal.status === 'yellow' ? 'default' : 'outline'}
                      className="w-full"
                    >
                      Yellow
                    </Button>
                    <Button 
                      variant={signal.status === 'green' ? 'default' : 'outline'}
                      className="w-full"
                    >
                      Green
                    </Button>
                  </div>
                </div>

                <div className="text-sm text-gray-500">
                  Last Maintenance: {signal.lastMaintenance}
                </div>
              </div>
            </Card>
          ))}
        </div>

        {/* Traffic Flow Map */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Traffic Flow Map</h2>
          <div className="bg-gray-100 h-[400px] rounded-lg flex items-center justify-center">
            <p className="text-gray-500">Interactive traffic flow map would be integrated here</p>
          </div>
        </Card>

        {/* Schedule Management */}
        <Card className="p-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold">Signal Schedule</h2>
            <Button>Add New Schedule</Button>
          </div>
          <div className="bg-gray-100 p-4 rounded-lg">
            <p className="text-gray-500 text-center">Signal timing schedules and patterns would be configured here</p>
          </div>
        </Card>
      </div>
    </Layout>
  )
}
